import StoryDate from '../StoryDate/StoryDate';
import StoryTitle from '../StoryTitle/StoryTitle';
import Paragraph from '../Paragraph/Paragraph';
import './StoryCard.scss';
import StoryShort from '../StoryShort/StoryShort';
import StoryButton from '../StoryButton/StoryButton';
import AOS from "aos";
import "aos/dist/aos.css";
import React, { Component }  from 'react';

function StoryCard(props){

    return(<div style={props.style} className="StoryCard">
        <StoryTitle title={props.story.title}/>
        <StoryDate year={props.story.year}/>
        <StoryShort text={props.story.shortStory}/>
       
        <StoryButton onClick={props.onClick}/>
    </div>);
}

export default StoryCard;