import StoryCard from '../StoryCard/StoryCard';
import StoryImage from '../StoryImage/StoryImage';
import './Story.scss';
import React, { Component }  from 'react';

function Story(props){
    return(<div style={props.style} className={`Story ${props.className}`}>

        <StoryImage src={props.story.srcName}/>
        <StoryCard onClick={props.onClick} story={props.story}/>
       


    </div>);
}

export default Story;