import './Navbar.scss';
import Container from '../Container/Container';
import { Link } from 'react-scroll';
import disableScroll from 'disable-scroll';
import React, { Component }  from 'react';

function Navbar(props){
    return (
        <div>
        <Container className="NavbarContainer">
            <ul>
                <li><Link onClick={()=>{props.onClickDown()}} to="home" offset={-25} spy={true} smooth={true} duration={600}><a>HOME</a></Link></li>
                <li><Link onClick={()=>{props.onClickDown()}} to="stories" offset={-25} spy={true} smooth={true} duration={600}><a>STORIES</a></Link></li>
                <li><Link onClick={()=>{props.onClickDown()}} to="contact" offset={-25} spy={true} smooth={true} duration={600}><a>CONTACT ME</a></Link></li>
            </ul>
        </Container>

        <Container className="NavbarContainer__mobile">
            <nav className="menu--mobile">
      <input className="menu-btn" type="checkbox" id="menu-btn" />
      <label className="menu-icon" htmlFor="menu-btn"
        ><span className="navicon"></span
      ></label>
      <ul className="menu--mobile__list">

      <li><Link onClick={()=>{props.onClickDown()}} className="menu--mobile__element" to="home" offset={-25} spy={true} smooth={true} duration={600}>HOME</Link></li>
      <li><Link onClick={()=>{props.onClickDown()}} className="menu--mobile__element" to="stories" offset={-25} spy={true} smooth={true} duration={600}>STORIES</Link></li>
      <li><Link onClick={()=>{props.onClickDown()}} className="menu--mobile__element" to="contact" offset={-25} spy={true} smooth={true} duration={600}>CONTACT ME</Link></li>

       
      </ul>
    </nav>
        </Container>
        
    </div>

    );
}

export default Navbar;