import './ButtonDown.scss';
import React, { Component }  from 'react';
function ButtonDown(props){
    return(
        <svg onClick={props.onClick} onMouseLeave={props.onMouseLeave} onMouseEnter={props.onMouseEnter} className='ButtonDown' style={props.style} version="1.0" xmlns="http://www.w3.org/2000/svg"
 width={props.width ? `${props.width}` : `24pt`} height={props.height ? `${props.height}` : `32pt`} viewBox="0 0 24.000000 24.000000"
 preserveAspectRatio="xMidYMid meet">

<g transform="translate(0.000000,24.000000) scale(0.100000,-0.100000)"
fill={props.fill ? `${props.fill}` :"#000000"} stroke={props.stroke ? `${props.stroke}` :"none"}>
<path d="M57 118 l63 -63 60 60 c61 60 66 93 7 38 -66 -61 -62 -60 -116 -14
-75 63 -84 49 -14 -21z"/>
</g>
</svg>

    );
}

export default ButtonDown;